
// import { SettingOutlined, EditOutlined, EllipsisOutlined } from '@ant-design/icons-vue';
import { defineComponent } from "@vue/runtime-core";
export default defineComponent({
    name: "PhotographyImage",
    setup() {
        return {}
    },
    data() {
        return {
            allImageData: [
                {
                    id: '13',
                    alt: '西湖烟柳垂钓者',
                    src: '/img/DSC01183.JPG',
                    description: '西湖烟柳垂钓者'
                },
                {
                    id: '12',
                    alt: '西湖烟柳2',
                    src: '/img/DSC01177.JPG',
                    description: '西湖烟柳2'
                },
                {
                    id: '11',
                    alt: '西湖烟柳1',
                    src: '/img/DSC01174.JPG',
                    description: '西湖烟柳1'
                },
                {
                    id: '10',
                    alt: '西湖沙滩的树跟天空',
                    src: '/img/DSC01171.JPG',
                    description: '西湖沙滩的树跟天空'
                },
                {
                    id: '9',
                    alt: '西湖沙滩的鸟巢',
                    src: '/img/DSC01168.JPG',
                    description: '西湖沙滩的鸟巢'
                },
                {
                    id: '8',
                    alt: '采摘园的油菜花',
                    src: '/img/DSC01166.JPG',
                    description: '采摘园的油菜花'
                },
                {
                    id: '1',
                    alt: '家门口的风景',
                    src: '/img/DSC01065.JPG',
                    description: '家门口的风景'
                },
                {
                    id: '2',
                    alt: '[北京]发小给流浪猫洗澡，2021年12月11日，12:52:18',
                    src: '/img/DSC00745.JPG',
                    description: '[北京]发小给流浪猫洗澡，2021年12月11日，12:52:18'
                },
                {
                    id: '3',
                    alt: '路边随手拍',
                    src: '/img/DSC01017.JPG',
                    description: '路边随手拍'
                },
                {
                    id: '4',
                    alt: '路边随手拍',
                    src: '/img/DSC01020.JPG',
                    description: '路边随手拍'
                },
                {
                    id: '5',
                    alt: '小清新',
                    src: '/img/DSC01024.JPG',
                    description: '小清新'
                },
                {
                    id: '6',
                    alt: '朋友的核桃',
                    src: '/img/DSC00900.jpg',
                    description: '朋友的核桃'
                },
                {
                    id: '7',
                    alt: '未完工的小楼',
                    src: '/img/DSC01003.JPG',
                    description: '未完工的小楼'
                }
            ],
            imageData: [{
                id: '1',
                alt: '家门口的风景',
                src: '/img/DSC01065.JPG',
                description: '家门口的风景'
            }],
            loadText: "",
            startNum: 0,
            endNum: 4,
        }
    },
    mounted() {
        // 初始化
        this.imageData = []
        // 如果总数据小于10，结束就等于总图片数量
        if (this.allImageData.length < 4) {
            this.endNum = this.allImageData.length
            this.loadText = ""
        }
        this.loadMoreImage()
    },
    methods: {
        loadMoreImage() {
            // this.imageData = [...this.imageData, ...this.allImageData.splice(start, end)]
            for (let i = this.startNum; i < this.endNum; i++) {
                this.imageData.push(this.allImageData[i])
            }
            // 查看是否还有更多图片
            this.setStartEnd()
        },
        setStartEnd() {
            if (this.endNum > this.allImageData.length) {
                this.endNum = this.allImageData.length;
                this.startNum += 4
                this.loadText = "加载更多"
            } else if (this.endNum == this.allImageData.length) { //  && this.imageData.length != this.allImageData.length
                this.loadText = ""
            } else {
                this.endNum += 4
                this.startNum += 4
                this.loadText = "加载更多"
                // 如果结束加完比总数大，就直接等于总数
                if (this.endNum >= this.allImageData.length) {
                    this.endNum = this.allImageData.length
                }
            }
        }
    },
    components: {
        // SettingOutlined,
        // EditOutlined,
        // EllipsisOutlined,
    },
})
